import request from "./http";

// 发现页
export function getPageDiscovery(){
    return request({
        url:"page/discovery",
        method:"get"
    })
}

// 搜索页热门关键词
export function getRecommendKws(){
    return request({
        url:"search/recommend_kws",
        method:"get"
    })
}

/**
 * 搜索
 * @param {String} type 搜索类型 (article文章 creator作者)
 * @param {String} kw 关键词
 * @param {String} sort 排序方式 (defalut 默认, addtime 最新, hot 最热)
 */
export function getSearchInfo(params){
    return request({
        url:"search",
        method:"get",
        params
    })
}

/**
 * 分类视频
 * @param {String} category_id 分类id
 * @param {String} page 页码
 */
 export function getCategoryArticles(params){
    return request({
        url:"articles",
        method:"get",
        params
    })
}

