<template>
  <div class="categoryview">
    <van-sticky>
      <div class="categoryTop">
        <span class="iconfont icon-xiangzuojiantou" @click="goPrev"></span>
        <p>{{cname}}</p>
        <div class="right"></div>
      </div>
    </van-sticky>
    <div class="works">
      <div class="worksRuslt">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <div v-if="worksList">
            <router-link
            tag="div"
            :to="'/videoplay?id=' + item.id"
            class="videoInfo"
            v-for="item in worksList"
            :key="item.id"
          >
            <div class="Bigimg">
              <img :src="item.cover" alt="" />
            </div>
            <div class="detailInfo">
              <h4>{{ item.title }}</h4>
              <div class="count" v-if="item.count">
                播放 {{ item.count.count_view }} · 喜欢
                {{ item.count.count_like }}
              </div>

              <div class="allAuthor" v-if="item.team[0]">
                <div class="pics" >
                  <div class="img img1" v-if="item.team[0]">
                    <img :src="item.team[0].userinfo.avatar" alt="" />
                  </div>
                  <div class="img img2" v-if="item.team[1]">
                    <img :src="item.team[1].userinfo.avatar" alt="" />
                  </div>
                  <div class="img img3" v-if="item.team[2]">
                    <img :src="item.team[2].userinfo.avatar" alt="" />
                  </div>
                </div>
                <div class="all">
                  <span class="firstname" v-if="item.team[0]">{{
                    item.team[0].userinfo.username
                  }}</span
                  ><span>等{{ item.team_user_count }}位创作人</span>
                </div>
              </div>
              <div class="allAuthor" v-else>
                <div class="pic">
                  <div class="img img1" v-if="item.author">
                    <img :src="item.author.userinfo.avatar" alt="" />
                  </div>
                </div>
                <div class="all" v-if="item.author">{{ item.author.userinfo.username }}</div>
              </div>
            </div>
          </router-link>
          </div>
        </van-list>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategoryArticles } from "../api/search";
export default {
  data() {
    return {
      page: 1,
      worksList: null,
      cid: 0,
      loading: false,
      finished: false,
      cname:''
    };
  },
  methods: {
    onLoad() {
      this.cid = this.$route.query.cid;
      (this.cid);
      getCategoryArticles({ category_id: this.cid, page: this.page }).then(
        (data) => {
          // console.log(data);
          let newData = data.data.list;
        //   this.worksData = data.data;
        if (this.page > 1) {
            for (let i = 0; i < newData.length; i++) {
              this.worksList.push(newData[i]);
            }
          } else {
            // 第一页
            this.worksList = newData;
            let index = data.data.header.categories.findIndex(item=>item.value == this.cid)
            this.cname = data.data.header.categories[index].title;
          }
          // console.log(this.worksList);
          // 加载状态结束
          this.loading = false;

          // 数据全部加载完成
          if (this.worksList.length >= 30) {
            this.finished = true;
          }
          this.page += 1;
        }
      );
    },
    goPrev() {
      this.$router.go(-1);
    },
  },
  created() {
  },
};
</script>

<style lang="less">
.categoryview {
  .categoryTop {
    width: 100%;
    height: 42px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    border-bottom: 1px solid #f7f7f7;
    .iconfont {
      font-size: 15px;
      font-weight: bold;
      padding-left: 20px;
    }
    p {
      font-size: 16px;
    }
    .right {
      width: 15px;
      height: 15px;
      padding-right: 20px;
    }
  }
  .works {
    .worksRuslt {
      padding: 0 20px;
      .videoInfo {
        padding: 15px 0;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #f7f7f7;
        .Bigimg {
          width: 160px;
          height: 90px;
          border-radius: 5px;
          overflow: hidden;
          box-shadow: 0px 4px 4px 0px #ddd;
          img {
            width: 160px;
            height: 90px;
          }
        }
        .detailInfo {
          flex: 1;
          padding-left: 12px;
          h4 {
            font-size: 13px;
            line-height: 19px;
            white-space: wrap;
            font-weight: bold;
          }
          .count {
            height: 26px;
            line-height: 26px;
            font-size: 12px;
            color: #b0b0b0;
          }
          .allAuthor {
            height: 20px;
            display: flex;
            .pics {
              width: 36px;
              height: 20px;
              position: relative;
              .img {
                width: 16px;
                height: 16px;
                overflow: hidden;
                border-radius: 50%;
                border: 2px solid #fff;
                position: absolute;
                img {
                  width: 16px;
                  height: 16px;
                }
                &.img1 {
                  left: 0;
                  top: 0;
                  z-index: 3;
                }
                &.img2 {
                  left: 8px;
                  top: 0;
                  z-index: 2;
                }
                &.img3 {
                  left: 16px;
                  top: 0;
                  z-index: 1;
                }
              }
            }
            .pic {
              .img {
                width: 16px;
                height: 16px;
                overflow: hidden;
                border-radius: 50%;
                border: 2px solid #fff;
                // position: absolute;
                img {
                  width: 16px;
                  height: 16px;
                }
              }
            }
            .all {
              height: 20px;
              line-height: 20px;
              font-size: 12px;
              margin-left: 6px;
              color: #6b6b6b;
              display: flex;
              align-items: center;
              span.firstname {
                font-size: 12px;
                height: 20px;
                line-height: 20px;
                display: inline-block;
                width: 30px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
          }
        }
      }
    }
  }
}
</style>